.navbarWrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background: var(--bg-glass);
    backdrop-filter: blur(12px);
    border-bottom: 1px solid var(--border-subtle);
    height: 64px;
    display: flex;
    align-items: center;
}

.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 18px;
    font-weight: 700;
    color: var(--text-main);
}

.navLinks {
    display: flex;
    align-items: center;
    gap: 32px;
}

.link {
    font-size: 14px;
    font-weight: 500;
    color: var(--text-secondary);
    transition: color 0.2s ease;
}

.link:hover {
    color: var(--text-main);
}

.ctaGroup {
    display: flex;
    align-items: center;
    gap: 16px;
}

.signInBtn {
    font-size: 14px;
    font-weight: 500;
    color: var(--text-secondary);
}

.getStartedBtn {
    background: var(--primary);
    color: white;
    padding: 8px 16px;
    border-radius: var(--radius-sm);
    font-size: 14px;
    font-weight: 600;
    transition: transform 0.1s ease;
}

.getStartedBtn:hover {
    transform: translateY(-1px);
}

@media (max-width: 768px) {
    .navLinks {
        display: none;
    }
}