.footer {
    background: var(--bg-body);
    /* Seamless with body */
    border-top: 1px solid var(--border);
    font-size: 12px;
    color: var(--text-secondary);
    padding: 48px 0;
}

.content {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    /* Logo area wider */
    gap: 48px;
    margin-bottom: 32px;
}

.brand h3 {
    font-size: 14px;
    font-weight: 600;
    color: var(--text-main);
    margin-bottom: 8px;
}

.column {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.column h4 {
    font-size: 12px;
    font-weight: 600;
    color: var(--text-main);
    margin-bottom: 4px;
}

.link {
    color: var(--text-secondary);
    transition: color 0.2s ease;
}

.link:hover {
    color: var(--text-main);
    text-decoration: underline;
}

.copyright {
    padding-top: 24px;
    border-top: 1px solid var(--border-subtle);
    color: var(--text-muted);
}

@media (max-width: 768px) {
    .content {
        grid-template-columns: 1fr;
        gap: 32px;
    }
}